import React from 'react';
import {Handler, Model, Pathway, PathwayId} from '../model';
import EditPathway from './EditPathway';
import SelectOrCreatePathway from './SelectOrCreatePathway';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBoxes, faBezierCurve, faEdit, faPlusCircle, faDownload, faUpload } from '@fortawesome/free-solid-svg-icons';
import { saveAs } from 'file-saver';
import ImportData from './ImportData';

export interface Props {
    model : Model,
    become : Handler<(props : Props) => React.ReactElement>,
    updatePathway : Handler<Pathway>,
    deletePathway : Handler<PathwayId>,
    setModel : Handler<Model>
}

export function ToolbarHome(props : Props) {
    return (<nav className="panel">
        <div className="panel-heading">Select an Action</div>

        <p className="panel-block" onClick={(_) => null}>
            <span className="panel-icon">
                <FontAwesomeIcon icon={faBoxes} />
            </span>
            Create Node
        </p>

        <p className="panel-block" onClick={(_) => null}>
            <span className="panel-icon">
                <FontAwesomeIcon icon={faBezierCurve} />
            </span>
            Create Link
        </p>

        <label className="panel-block" onClick={(_) => props.become(CreatePathway)}>
            <span className="panel-icon">
                <FontAwesomeIcon icon={faPlusCircle} />
            </span>
            Create Pathway
        </label>

        <label className="panel-block" onClick={(_) => props.become(SelectPathwayToEdit)}>
            <span className="panel-icon">
                <FontAwesomeIcon icon={faEdit} />
            </span>
            Edit Pathway
        </label>

        <label className="panel-block" onClick={(_) => props.become(ImportDataAction)}>
            <span className="panel-icon">
                <FontAwesomeIcon icon={faUpload} />
            </span>
            Import Data
        </label>

        <label className="panel-block" onClick={(_) => props.become(ExportData)}>
            <span className="panel-icon">
                <FontAwesomeIcon icon={faDownload} />
            </span>
            Export Data
        </label>

    </nav>);
}


/**** Workflow for the "create pathway" option ****/

function CreatePathway(props : Props) {
    const pathwayIds = props.model.pathways.map((p : Pathway) => p.id);
    const newId = pathwayIds.length > 0 ? 1 + Math.max(...pathwayIds) : 1;

    function onSubmit(p : Pathway) {
        props.updatePathway(p);
        props.become(ToolbarHome);
    }

    return <EditPathway {...props.model} id={newId} onSubmit={onSubmit} 
                goBack={(_) => props.become(ToolbarHome)}
                title="Create a Pathway"
                submitButtonText="Create" />
}


/**** Workflow for the "edit pathway" option ****/

function SelectPathwayToEdit(props : Props) {
    return <SelectOrCreatePathway pathways={props.model.pathways} 
                goBack={(_) => props.become(ToolbarHome)}
                onSelect={(id : PathwayId) => props.become(EditThisPathway(id))} />;
}

function EditThisPathway(id : PathwayId) {
    return function (props : Props) {

        function onSubmit(p : Pathway) {
            props.updatePathway(p);
            props.become(SelectPathwayToEdit);
        }

        function onDelete(id : PathwayId) {
            props.deletePathway(id);
            props.become(SelectPathwayToEdit);
        }

        return <EditPathway {...props.model} id={id} onSubmit={onSubmit} 
                    onDelete={onDelete}
                    goBack={(_) => props.become(SelectPathwayToEdit)}
                    title="Edit Pathway"
                    submitButtonText="Confirm changes" />;
    };
}


/**** Workflow for the "export data" option ****/

function ImportDataAction(props : Props) {

    function onUpload(model : Model) {
        props.setModel(model);
        props.become(ToolbarHome);
    }

    return <ImportData {...props.model} 
                goBack={() => props.become(ToolbarHome)} 
                setModel={onUpload} />
}


/**** Workflow for the "export data" option ****/

function ExportData(props : Props) {
    const blob = new Blob([JSON.stringify(props.model, null, 2)], {type: "application/json;charset=utf-8"});
    saveAs(blob, "atlas.json");
    return ToolbarHome(props);
}