export interface Model {
    assets : Array<Node>,
    links : Array<Link>,
    pathways : Array<Pathway>
}

export interface Node {
    id : NodeId,
    name : string,
    color : Color
}

export interface Link {
    src : NodeId,
    dst : NodeId,
    pathway : PathwayId
}

export interface Pathway {
    id : PathwayId,
    name : string,
    color : Color
}

export type Color                  =  'light-blue' | 'purple' | 'light-green' | 'red' | 'orange-yellow';
export const Colors : Array<Color> = ['light-blue' , 'purple' , 'light-green' , 'red' , 'orange-yellow'];

export type NodeId = number;
export type PathwayId = number;
export type Handler<T> = (e : T) => void;