import React from 'react';
import {Model, Pathway, PathwayId, Link} from '../model';
import { ToolbarHome, Props as ModeProps } from './ToolbarModes';
import Icon from './Icon';
import { faRedo, faUndo } from '@fortawesome/free-solid-svg-icons';


const DB = "AppState";
const PERSIST_DATA_TIMEOUT = 5000;
const MAX_UNDO_SIZE = 50;


interface Data {
    model : Model,
    undo  : Model[],
    redo  : Model[],
}

interface State {
    mode  : (props : ModeProps) => React.ReactElement,
    data  : Data,
    dirty : Boolean,
}

const initialData : Data = {
    model: {
        assets: [],
        links: [],
        pathways: [],
    },
    undo: [],
    redo: [],
}

const initialState : State = {
    mode: ToolbarHome,
    data: initialData,
    dirty: false,
};


function deepCopy(obj : Object) {
    return JSON.parse(JSON.stringify(obj));
}


class Controller extends React.Component<{},State> {

    constructor(props : any) {
        super(props);

        const savedState = localStorage.getItem(DB);
        if (savedState === null) {
            this.state = {...initialState};
        }
        if (savedState !== null) {
            const data = JSON.parse(savedState) as Data;
            this.state = Object.assign(initialState, {data});
        }

        this.become = this.become.bind(this);
        this.deletePathway = this.deletePathway.bind(this);
        this.updatePathway = this.updatePathway.bind(this);
        this.undo = this.undo.bind(this);
        this.redo = this.redo.bind(this);
        this.setModel = this.setModel.bind(this);
        this.persistData = this.persistData.bind(this);
    }

    become(mode : (props : ModeProps) => React.ReactElement) {
        this.setState({mode: mode});
    }

    undo() {
        const data = this.state.data;
        if (data.undo.length > 0) {
            // Make a copy of 'redo' with the current model pushed onto it
            const redo = data.redo.slice();
            redo.push(data.model);

            // Make a copy of 'redo' with the new model popped from it
            const undo = data.undo.slice();
            const model = undo.pop() as Model;

            if (!this.state.dirty) {
                setTimeout(this.persistData, PERSIST_DATA_TIMEOUT);
            }
            this.setState({ data: { model, undo, redo }, dirty: true });
        }
    }

    redo() {
        const data = this.state.data;
        if (data.redo.length > 0) {
            // Make a copy of 'undo' with the current model pushed onto it
            const undo = data.undo.slice();
            undo.push(data.model);

            // Make a copy of 'redo' with the new model popped from it
            const redo = data.redo.slice();
            const model = redo.pop() as Model;

            if (!this.state.dirty) {
                setTimeout(this.persistData, PERSIST_DATA_TIMEOUT);
            }
            this.setState({ data: { model, undo, redo }, dirty: true });
        }
    }

    /**
     * This method allows us to conveniently modify the underlying Model while
     * automatically managing the undo history.
     * - Provides a deep copy of the current model to the method for mutation
     * - Throws away the current redo buffer
     * - Appends the old model to the undo buffer
     * 
     * @param callback This function will be passed a copy of the current model
     *    that can be freely mutated. The returned value will be the new model.
     */
    setData(callback : (model : Model) => Model) {
        // Make a copy of 'undo' with the current model pushed to it
        // Throw away the 'redo' buffer
        // Provide a mutable copy of the model to use
        const data = this.state.data;
        const redo : Model[] = [];
        const undo = data.undo.slice();
        undo.push(data.model);
        const model = deepCopy(data.model) as Model;

        // Now that we've made a modification, schedule a file storage event
        if (!this.state.dirty) {
            setTimeout(this.persistData, PERSIST_DATA_TIMEOUT);
        }
        // If the undo buffer is too large, drop the oldest element
        if (undo.length > MAX_UNDO_SIZE) {
            undo.shift();
        }

        this.setState({ 
            data: { undo, redo, model: callback(model)},
            dirty: true 
        });
    }


    persistData() {
        localStorage.setItem(DB, JSON.stringify(this.state.data));
        this.setState({ dirty: false });
    }


    deletePathway(id : PathwayId) {
        this.setData((model : Model) => {
            // Remove this pathway from the list of pathways
            const i = model.pathways.findIndex((p : Pathway) => p.id === id);
            model.pathways.splice(i, 1);

            // Remove all links belonging to this pathway
            model.links = model.links.filter((link : Link) => link.pathway !== id);

            return model;
        })
    }

    updatePathway(pathway : Pathway) {
        this.setData((model : Model) => {
            // If this pathway is new, add it; otherwise modify the existing entry
            const idx = model.pathways.findIndex((p) => p.id === pathway.id);
            if (idx === -1) {
                model.pathways.push(pathway);
            }
            else {
                model.pathways[idx] = pathway;
            }
            return model;
        })
    }

    setModel(model : Model) {
        this.setData((_ : Model) => model);
    }

    render() {
        return (
            <div>
            <div style={{position: "fixed", top: "2rem", left: "1.5rem", maxWidth: "400px"}}>
            {
                this.state.mode({
                    model: this.state.data.model,
                    become: this.become,
                    updatePathway: this.updatePathway,
                    deletePathway: this.deletePathway,
                    setModel: this.setModel
                })
            }
            </div>
            <div style={{position: "fixed", top: "2rem", right: "1.5rem"}}>
                <div className="field has-addons">
                <p className="control">
                    <button className="button" 
                        onClick={this.undo}
                        disabled={this.state.data.undo.length === 0}>
                    <span className="icon is-small">
                        <Icon icon={faUndo} />
                    </span>
                    <span>Undo</span>
                    </button>
                </p>
                <p className="control">
                    <button className="button" 
                        onClick={this.redo}
                        disabled={this.state.data.redo.length === 0}>
                    <span className="icon is-small">
                        <Icon icon={faRedo} />
                    </span>
                    <span>Redo</span>
                    </button>
                </p>
                </div>
            </div>
            </div>
        );
    }
}

export default Controller;