import React from 'react';
// import Dropzone from 'react-dropzone';
import { faArrowLeft, faUpload } from '@fortawesome/free-solid-svg-icons'
import {Model, Handler} from '../model';
import Icon from './Icon';

interface Props extends Model {
    goBack : Handler<any>,
    setModel : Handler<Model>
}

export default class ImportData extends React.Component<Props,{}> {
    input : React.RefObject<HTMLInputElement>

    constructor(props : Props) {
        super(props);
        this.input = React.createRef();
        this.handleUpload = this.handleUpload.bind(this);
    }

    handleUpload(event : React.ChangeEvent<HTMLInputElement>) {
        const f = this.input.current as HTMLInputElement;
        const files = f.files as FileList;

        const reader = new FileReader();
        reader.onabort = () => console.log('file reading was aborted')
        reader.onerror = () => console.log('file reading has failed')
        reader.onload  = () => {
            console.log(reader.result);
            const model = JSON.parse(reader.result as string) as Model;
            console.log(model);
            this.props.setModel(model);
        }
        reader.readAsText(files[0]);
    }

    importFile<T extends File>(acceptedFiles : T[]) {
    }


    render() {
        return (
            <nav className="panel">

                <div className="panel-heading">
                <div className="level">
                    <div className="level-left">
                        <div className="level-item">
                            <button className="button" onClick={this.props.goBack}>
                                <Icon icon={faArrowLeft} />
                            </button>
                        </div>
                        <div className="level-item">
                            <p>Import Data File</p>
                        </div>
                    </div>
                </div>
                </div>

                <div className="panel-block" style={{padding: "3rem"}}>
                <div className="file is-boxed is-centered is-vcentered">
                    <label className="file-label">
                        <input className="file-input" type="file" ref={this.input} onChange={this.handleUpload} />
                        <span className="file-cta">
                            <span className="file-icon">
                                <Icon icon={faUpload} />
                            </span>
                            <span className="file-label">
                                Choose a fileâ€¦
                            </span>
                        </span>
                    </label>
                </div>
                </div>

            </nav>);
    }
}