import React from 'react';
import Icon from './Icon';
import { faCircle, faPlus, faArrowLeft } from '@fortawesome/free-solid-svg-icons'
import {Handler,Pathway,PathwayId} from '../model';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

interface Props {
    pathways : Array<Pathway>, 
    onCreate? : Handler<any>,
    onSelect : Handler<PathwayId>,
    goBack : Handler<any>,
}

export default function SelectOrCreatePathway(props : Props) {
    return (<nav className="panel">
        <div className="panel-heading">
            <div className="level">
                <div className="level-left">
                    <div className="level-item">
                        <button className="button" onClick={props.goBack}>
                            <Icon icon={faArrowLeft} />
                        </button>
                    </div>
                    <div className="level-item">
                        <p >Pathways</p>
                    </div>
                </div>
            </div>
        </div>

        {props.pathways.map((pathway) => 
            <PathwayItem key={pathway.name} {...pathway} selectPathway={props.onSelect} />
        )}

        {props.onCreate &&
            <div className="panel-block">
                <button onClick={props.onCreate} type="button" className="button is-white is-fullwidth">
                    <Icon icon={faPlus} />
                    <span> Create a new pathway</span>
                </button>
            </div>
        }
    </nav>);
}

function PathwayItem(props : Pathway & { selectPathway : Handler<PathwayId> }) {
    return (<label className="panel-block" onClick={(_) => props.selectPathway(props.id)}>
                <span className={"panel-icon has-text-" + props.color}>
                    <FontAwesomeIcon icon={faCircle} />
                </span>
                {props.name}
            </label>);
}